<template>
  <div>
    <img :src="v.img" v-for="(v,i) in list" :key="i" alt />
  </div>
</template>

<script>
import { mhDetailsApi } from "@/api/api";

export default {
  name: "",
  data() {
    return {
      list: []
    };
  },
  methods: {},
  created() {
    let { url } = this.$route.query;
    mhDetailsApi(url).then(res => {
      let { code, list } = res.data;
      if(code === 0){
        this.list=list;
      }
    });
  }
};
</script>

<style lang='less' scoped>
img {
  width: 100%;
}
</style>